import { useState } from "react";
import "./Movie.scss";

export default function Movie({ year, title, summary, poster, genres }) {
  const [showAll, setShowAll] = useState(false);

  const showAllSummary = () => {
    setShowAll(!showAll);
  };

  const summerize = (summary) => {
    return summary.length > 150 ? (
      <p className="movie_summary">
        {!showAll ? summary.slice(0, 150) : summary}
        <button className="summary-btn" onClick={showAllSummary}>
          {!showAll ? `...더보기` : `숨기기`}
        </button>
      </p>
    ) : (
      <p>{summary}</p>
    );
  };

  return (
    <div className="movie">
      <img src={poster} alt={title} title={title} />
      <div className="movie_data">
        <h3 className="movie_title">{title}</h3>
        <h5 className="movie_year">{year}</h5>
        <ul className="movie_genres">
          {genres.map((genre, index) => {
            return (
              <li className="movie_genre" key={index}>
                {genre}
              </li>
            );
          })}
        </ul>
        {summerize(summary)}
      </div>
    </div>
  );
}
